.modal,
.board_opening {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-body {
  padding: 2rem;
  border-radius: 5px;
  background: #fff;
  max-width: 560px;
  height: auto;
  width: 100%;
  margin: 15px;
}
