* {
  box-sizing: border-box;
}
html,
body {
  padding: 0;
  margin: 0;
  font-size: 20px;
  line-height: 1.5;
  background-color: #ffc10727 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  text-decoration: none;
  color: rgb(73, 73, 73) !important;
}
a:hover {
  text-decoration: none !important;
  color: rgb(214, 205, 205) !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
input,
button {
  cursor: pointer;
  border: none;
  outline: none;
}
button:focus {
  outline: none;
}
.header {
  background-color: #ffc1072e;
}
.header .container {
  padding: 15px 0;
  display: flex;
  padding: 15px 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main {
  height: 100%;
}
.main .container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0 40px;
}

.footer-container {
  margin-top: 50px;
  background-color: #ffc1072e;
}
.footer {
  padding: 15px 0;
}
.btn_audio.btn {
  position: absolute;
  padding: 2px !important;
  right: 25px;
  top: 20px;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  text-align: center !important;
  border-radius: 100% !important;
}
.btn_mode_active12 {
  background-color: #cbe6fa !important;
}
.btn_mode_active24 {
  background-color: #faec9f !important;
}
.btn_mode_active36 {
  background-color: #f58b8b !important;
}

.modal,
.board_opening {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-body {
  padding: 2rem;
  border-radius: 5px;
  background: #fff;
  max-width: 560px;
  height: auto;
  width: 100%;
  margin: 15px;
}

.card-game {
  width: 100px;

  height: 150px;

  margin: 5px;
  transition-duration: 1s;
  cursor: pointer;
}
.card-body {
  padding: 0;
  transition: 1s;
}
.image-card {
  width: 100px;
  height: 150px;
  border-radius: 0.25rem;
}
.card.card-back {
  transform: rotateY(180deg);
}
.board {
  position: relative;
  display: flex;
  margin: 0 15px 0;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 900px;

  width: 100%;
  height: auto;
  border-radius: 5px;
  padding: 10px;
}

.board_max {
  margin: 0 15px;
  max-width: 1500px;
}
.board_container {
  display: flex;
}

.btn-game {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: -12px;
  top: -12px;
  padding: 2px;
  z-index: 2;
  border-radius: 100% !important;
}
.newGame {
  margin: 10px 0;
  transition: 3s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
@media all and (max-width: 1058px) {
  .card-game {
    width: 90px;
    height: 140px;
  }
  .image-card {
    width: 90px;
    height: 140px;
  }
}
@media all and (max-width: 1040px) {
  .card-game {
    width: 85px;
    height: 130px;
  }
  .image-card {
    width: 85px;
    height: 130px;
  }
}
@media all and (max-width: 950px) {
  .card-game {
    width: 70px;
    height: 115px;
  }
  .image-card {
    width: 70px;
    height: 115px;
  }
}
@media all and (max-width: 747px) {
  .card-game {
    width: 60px;
    height: 105px;
  }
  .image-card {
    width: 60px;
    height: 105px;
  }
}

.game_menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}
.game_statistics {
  margin-top: 15px;
}

